<template>
<div id="menu">
	<ul v-if="!datas.data == ''" :style="{ background: datas.params.bgcolor }">
		<li v-for="item in datas.data">
			<a :href="item.hrefurl|| 'javascript:;'">
				<img :src="item.imgurl" :style="{borderRadius:datas.params.style}">
				<h5 :style="{'color':item.color}">{{item.text}}</h5>
			</a>
		</li>
	</ul>

</div>
</template>
<script>

  export default {
    props: ['datas'],
    mounted(){

    },
    methods:{

    }
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
	#menu{
		ul{display: flex;padding: 10px 0;
			li{flex: 1;
				img{width: 60%;}
			}
		}
	}
</style>


