<template>
<div id="all">
	
	<div class="home" v-if="!oftemp">
		<c-title hide="true" text='home'></c-title>
		<div :class="{'search':'ture','mout':amout}" id="d1">
			<div class="search-form-box" @click='tosearch'><i class="el-icon-search"></i>输入关键词搜索</div>
		</div>

	<div class="m-swipe">
		<mt-swipe :auto="4000">
			<mt-swipe-item v-for="item in bannerData">
				<template v-if='item.thumb'><img :src="item.thumb"></template>
				<template v-if='!item.thumb'><img src="../../assets/images/img_default.png"></template>
			</mt-swipe-item>
		</mt-swipe>
	</div>
		<div class="ment">
			<c-ment :datas="category"></c-ment>
		</div>
		<div class="goodsList">
			<c-goodsList :goods='goodsListData' text='推荐商品'></c-goodsList>
		</div>
		<div style="height: 50px;"></div>
		</el-input>
	</div>
	<div class="temp" v-if='oftemp'>
		<div class="home">
			<template v-for="item in tempData">
				<component :is="item.temp" :datas='item'></component>
			</template>
			<div style="height: 53px;"></div>
		</div>
	</div>
</div>
</template>


<script>
	import home from './home_controller';
	export default home;
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	@import '../../assets/css/index.scss';
	
</style>


